    <!-- end -  nav-icons -->
    <m-card-list icon="Menu" title="新闻资讯" :categories="newCats">
      <template #items="{ category }">
        <router-link :to="`/articles/${news._id}`" tag="div" class="d-flex mt-3" v-for="(news, i ) in category.newsList" :key="i">
            <span>[{{news.categoryName}}]</span>
            <span class="ml-1">|</span>
            <span class="ml-1 flex-1 text-gray text-ellipsis pr-2"> {{news.title}}</span>
            <span class="text-lgray text-sm" >{{news.updatedAt | date}}</span>
        </router-link >
      </template>
    </m-card-list>
    <!-- end -  news -->
    <m-card-list icon="superhero-" title="英雄列表" :categories="heroCats">
      <template #items="{ category }">
        <div class="d-flex flex-wrap" style="margin: 0 -0.5rem">
          <router-link tag="div" :to="`/heroes/${hero._id}`" class="p-2 text-center" style="width:20%" v-for="(hero, i ) in category.heroList" :key="i">
          <img :src="hero.avatar" alt="" class="w-100" >
          <div>{{hero.name}}</div>
        </router-link>
        </div>
      </template>
    </m-card-list>
    <!-- end - hero -->
    <m-card icon="l-moive" title="精彩视频"></m-card>
