<template>
        <m-card :icon="icon" :title="title">
      <div class="card-body">
        <div class="nav jc-between">
          <div class="nav-item" :class="{active: active == i}"  v-for="(item, i) in categories" :key="i">
          <div class="nav-link" @click="$refs.list.$swiper.slideTo(i)">{{item.name}}</div>
          </div>
        </div>
         <swiper :options="{autoHeight: true}" class="mt-2" ref="list" @slide-change = "() => active = $refs.list.$swiper.realIndex">
        <swiper-slide v-for="(category, i) in categories" :key="i">
          <slot name="items" :category = "category"></slot>
        </swiper-slide>
      </swiper>
      </div>
    </m-card>
</template>
<script>
export default {
  props: {
    title: { type: String, required: true },
    icon: { type: String, required: true },
    categories: { type: Array, required: true }
  },
  data () {
    return {
      active: 0
    }
  }
}
</script>
<style lang="scss">
</style>
