<template>
  <div class="article" v-if="model">
    <div class="d-flex py-3 px-1 border-bottom">
      <div class="iconfont icon-back-line text-blur" @click="this.$router.go(-1)"></div>
      <strong class="flex-1 text-blur">
        {{model.title}}
      </strong>
      <div class="text-mgray text-sm">2020-5-7</div>
    </div>
    <div v-html="model.body" class="px-3 body"></div>
    <div class="px-3 border-top py-2">
      <div class="d-flex ai-center">
        <i class="iconfont icon-Menu"></i>
        <strong class="text-blur ml-1 text-lg">相关资讯</strong>
      </div>
      <div class="px-43 border-top py-2">
         <router-link class="py-1 text-sm text-ellipsis" :to="`/articles/${item._id}`" tag="div" v-for="item in model.related" :key="item._id">{{item.title}}</router-link>
      </div>
     
    </div>
  </div>
</template>
<script>
export default {
  props: {
    id: { required: true }
  },
  data () {
    return {
      model: null
    }
  },
  watch: {
    id: 'fetch'
  },
  created () {
    this.fetch()
  },
  methods: {
   async fetch() {
     const res = await this.$http.get(`articles/${this.id}`)
     this.model = res.data
     console.log(res.data)
    }
  }
}
</script>
